<template>
  <div class="article-recommend">
    <div class="tit">文章推荐</div>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <van-list
        v-model="loading"
        :finished="finished"
        @load="onLoad"
      >

        <ul class="article_list">
          <li v-for="(item, index) in articleList" :key="index">

<!--
            @click="$router.push({
            path: detailURL,
            query:{id: item.id}
            })"-->


            <h3 class="text-overflow" @click="details(item.id)">
              {{ item.title }}
            </h3>
            <p class="con-overflow" v-html="item.content">{{ item.content }}</p>
          </li>
        </ul>

      </van-list>
      <div class="no_more" v-show="finished">没有更多了......</div>
    </van-pull-refresh>

  </div>
</template>

<script>
    export default {
      name:"recommend",
      data(){
        return{
          detailURL: '/announceDetail',
          articleList: [],

          loading: false,
          finished: false,
          isLoading: false,
          pageNum: 1, // 页数
          offset: 0,  // 下次加载起始页
          totalPage: 0 // 总页数
        }
      },
      inject: ['reload'],
      methods:{
        details(id) {
          this.$router.push({
            path: '/announceDetail',
            query: {
              id: id
            }
          })

        this.reload()

        },
        async init(){
          let that = this;

          let params = {
            offset: (that.pageNum - 1) * 8
          };

          let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/messageList', params);

          that.articleList = res.result.messages.data;
          that.pageNum = res.result.messages.page; // 当前显示页
          that.totalPage = Math.ceil(res.result.messages.total / res.result.messages.limit); // 总页数

          if(res.result.messages.total<8) {
            that.finished = true; // 加载结束
            that.isLoading = false;
            that.loading = false;
            return false;
          }
         },

        async concatData(){
          let that = this;
           that.pageNum += 1;

          let params = {
            offset: (that.pageNum - 1) * 8
          };

          let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/messageList', params);
          if(res.result.messages.total<8) {
            that.finished = true; // 加载结束
            that.isLoading = false;
            that.loading = false;
            return false;
          }

          that.articleList = that.articleList.concat(res.result.messages.data);
          that.loading = false;

          if(that.pageNum >= that.totalPage){
            that.finished = true; // 加载结束
          }
        },

        onRefresh() {
          let that = this;
          setTimeout(() => {
            this.$toast({
              message: '刷新成功',
              position: 'bottom'
            });
            that.isLoading = false;
            that.pageNum = 1;
            that.loading = false;
            that.finished = false;
            that.isLoading = false;
            that.init();
          }, 500);
        },

        onLoad() {
          // 异步更新数据
          setTimeout(() => {
            this.concatData();
          }, 500);
        }
      },

      created(){
        this.init();
      }
    }
</script>

<style scoped lang="scss">
  $greyColor: #8D8D8D;
  $fontSize: 27px;

  .no_more{
    font-size: 30px;
    color: #8d8d8d;
    text-align: center;
    margin: 10px auto 50px;
  }

  .article-recommend{
    padding-top: 50px;
    .tit{
      height: 45px;
      line-height: 45px;
      font-size: $fontSize;
      padding-left: 30px;
      margin-bottom: 40px;
      position: relative;

      &:before{
        content: '';
        width: 13px;
        height: 100%;
        background: #13C1FE;
        position: absolute;
        left: 0;
        top: 0;
      }
    }

    ul.article_list{
      padding: 0 30px ;
      margin-bottom: 50px;

      li{
        padding-bottom: 25px;
        margin-bottom: 25px;
        &:not(:last-child){
          border-bottom: 5px solid #F5F5F5;
        }

        h3{
          color: #000;
          font-size: 30px;
          padding-bottom: 5px;
        }

        p{
          color: $greyColor;
          font-size: $fontSize;
        }
      }
    }
  }
</style>



